} */

::-webkit-scrollbar {
  width: 20px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  width: 10px;
  background: #f3f3f3;
}

/* Handle */
::-webkit-scrollbar-thumb {
  width: 10px;
  background: rgb(132, 204, 255);
  border-radius: 10px;
  cursor: pointer;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
  width: 10px;
  background: rgb(171, 247, 255);
}

